                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.dataAniversario2"
                            label="Data de Aniversário"
                            v-mask="'##/##/####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.telefone2"
                            label="Telefone"
                            v-mask="'(##) ####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.celular2"
                            label="Celular"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.whatsapp2"
                            label="Whatsapp"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.email2"
                            label="E-mail"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.redesSociais2"
                            label="Redes Sociais"
                            readonly
                          ></v-text-field>
                        </v-col>

                        <v-col cols="12" md="3" sm="12" xs="12">
                          <h4 class="mb-3 text-center">3</h4>
                          <v-text-field
                            v-model="info.nomeContato3"
                            label="Nome"
                            readonly
                          ></v-text-field>

                          <v-text-field
