                          ></v-text-field>

                          <v-text-field
                            v-model="info.celular3"
                            label="Celular"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.whatsapp3"
                            label="Whatsapp"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.email3"
                            label="E-mail"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.redesSociais3"
                            label="Redes Sociais"
                            readonly
                          ></v-text-field>
                        </v-col>

                        <v-col cols="12" md="3" sm="12" xs="12">
                          <h4 class="mb-3 text-center">4</h4>
                          <v-text-field
                            v-model="info.nomeContato4"
                            label="Nome"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.cargo4"
                            label="Cargo"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.dataAniversario4"
                            label="Data de Aniversário"
                            v-mask="'##/##/####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.telefone4"
                            label="Telefone"
                            v-mask="'(##) ####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.celular4"
                            label="Celular"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.whatsapp4"
                            label="Whatsapp"
                            v-mask="'(##) #####-####'"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.email4"
                            label="E-mail"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.redesSociais4"
                            label="Redes Sociais"
                            readonly
                          ></v-text-field>
                        </v-col>
                      </v-row>

                      <v-col class="mt-2 mb-2">
                        <h2
                          class="d-flex justify-center body-1 font-weight-black"
                        >
                          Informações de Registro
                        </h2>
                      </v-col>
                      <v-row class="mx-5 d-flex justify-center">
                        <v-col cols="12" md="6" sm="12" xs="12">
                          <v-text-field
                            v-model="info.datasContato"
                            label="Datas de Contato"
                            readonly
                          ></v-text-field>

                          <v-text-field
                            v-model="info.site"
                            label="Site"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6" sm="12" xs="12">
                          <v-text-field
                            v-model="info.status"
                            label="Status"
                            readonly
                          ></v-text-field>
                          <v-textarea
                            v-model="info.registros"
                            label="Considerações e Registros"
                            height="118"
                            filled
                            readonly
                          ></v-textarea>
                        </v-col>
                      </v-row>

                      <v-col class="mb-2 mt-2">
                        <h2
                          class="d-flex justify-center body-1 font-weight-black"
                        >
                          Informações do Produtor
                        </h2>
                      </v-col>

                      <v-row class="mx-5 d-flex justify-center">
                        <v-col cols="12" md="6" sm="12" xs="12">
                          <v-text-field
                            v-model="cultivares"
                            label="Cultivares"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.outroCultivo"
                            label='Citar cultivo caso o cultivo escolhido seja "Outro"'
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="6" sm="12" xs="12">
                          <v-text-field
                            v-model="info.metodo"
                            label="Método de Cultivo"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.outroMetodo"
                            label='Citar método caso método escolhido seja "Outro"'
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="3" sm="12" xs="12">
                          <v-text-field
                            v-model="info.areaFisica"
                            label="Área física total"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.cultivoParalelo"
                            label="Tem outro sistema de cultivo paralelo"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="3" sm="12" xs="12">
                          <v-text-field
                            v-model="info.areaHidroponico"
                            label="Área de cultivo hidropônico"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.migracao"
                            label="Migrou para hidroponia"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="3" sm="12" xs="12">
                          <v-text-field
                            v-model="info.volume"
                            label="Volume de produção total"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.profissao"
                            label="Profissão anterior"
                            readonly
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" md="3" sm="12" xs="12">
                          <v-text-field
                            v-model="info.localizacao"
                            label="Localização do cultivo"
                            readonly
                          ></v-text-field>
                          <v-text-field
                            v-model="info.movimentacao"
                            label="Movimentação financeira"
                            readonly
                          ></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-row justify="center">
                    <v-card-actions>
                      <v-btn color="#79F397" @click="closePesquisa">
                        Fechar
                      </v-btn>
                    </v-card-actions>
                  </v-row>
                </v-card>
              </v-dialog>
            </template>
            <template v-slot:[`item.visualizar`]="{ item }">
              <v-btn
                color="#79F397"
                @click="pesquisarItem(item)"
                class="white--text"
                rounded
              >
                Visualizar
              </v-btn>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import api from "../../service/api";
export default {
  name: "AppPesquisarProdutor",
  data() {
    return {
      dialog: false,
      dialogPesquisa: false,
      pesquisar: "",
      produtores: [],
      editedIndex: -1,
      headersPesquisar: [
        {
          text: "Nome",
          align: "center",
          sortable: false,
          value: "empresa",
        },
        {
          text: "Razão Social",
          align: "center",
          sortable: false,
          value: "razao_social",
        },
        {
          text: "Registro",
          align: "center",
          sortable: false,
          value: "registro",
        },
        {
          text: "País",
          align: "center",
          sortable: false,
          value: "pais",
        },
        {
          text: "Acão",
          value: "visualizar",
          sortable: false,
          align: "center",
        },
      ],
      info: {
        nomeFantasia: "",
        registro: "",
        razaoSocial: "",
        pais: "",
        uf: "",
        cidade: "",
        cep: "",
        endereco: "",
        atividade: "",
        nomeContato1: "",
        cargo1: "",
        dataAniversario1: "",
        telefone1: "",
        celular1: "",
        whatsapp1: "",
        email1: "",
        redesSociais1: "",
        nomeContato2: "",
        cargo2: "",
        dataAniversario2: "",
        telefone2: "",
        celular2: "",
        whatsapp2: "",
        email2: "",
        redesSociais2: "",
        nomeContato3: "",
        cargo3: "",
        dataAniversario3: "",
        telefone3: "",
        celular3: "",
        whatsapp3: "",
        email3: "",
        redesSociais3: "",
        nomeContato4: "",
        cargo4: "",
        dataAniversario4: "",
        telefone4: "",
        celular4: "",
        whatsapp4: "",
        email4: "",
        redesSociais4: "",
        datasContato: "",
        site: "",
        status: "",
        registros: "",
        cultivares: "",
        outroCultivo: "",
        metodo: "",
        outroMetodo: "",
        areaFisica: "",
        cultivoParalelo: "",
        areaHidroponico: "",
        migracao: "",
        volume: "",
        profissao: "",
        localizacao: "",
        movimentacao: "",
      },
    };
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  async created() {
    this.iniciar();
  },
  methods: {
    async iniciar() {
      const response = await api().get("produtores");
      this.produtores = response.data;
    },
    pesquisarItem(item) {
      this.editedIndex = this.produtores.indexOf(item);
      this.info = {
        nomeFantasia: item.nomeFantasia,
        registro: item.registro,
        pais: item.pais,
        cidade: item.cidade,
        uf: item.uf,
        endereco: item.endereco,
        razaoSocial: item.razao_social,
        atividade: item.atividade,
        nomeContato1: item.nome_1,
        contato1: item.telefone_1,
        celular1: item.celular_1,
        whatsapp1: item.whatsapp_1,
        email1: item.email_1,
        cargo1: item.cargo_1,
        dataAniversario1: item.data_aniversario_1,
        redesSociais1: item.redes_sociais_1,
        nomeContato2: item.nome_2,
        contato2: item.telefone_2,
        celular2: item.celular_2,
        whatsapp2: item.whatsapp_2,
        email2: item.email_2,
        cargo2: item.cargo_2,
        dataAniversario2: item.data_aniversario_2,
        redesSociais2: item.redes_sociais_2,
        nomeContato3: item.nome_3,
        contato3: item.telefone_3,
        celular3: item.celular_3,
        whatsapp3: item.whatsapp_3,
        email3: item.email_3,
        cargo3: item.cargo_3,
        dataAniversario3: item.data_aniversario_3,
        redesSociais3: item.redes_sociais_3,
        nomeContato4: item.nome_4,
        contato4: item.telefone_4,
        celular4: item.celular_4,
        whatsapp4: item.whatsapp_4,
        email4: item.email_4,
        cargo4: item.cargo_4,
        dataAniversario4: item.data_aniversario_4,
        redesSociais4: item.redes_sociais_4,
        datasContato: item.datas_contato,
        site: item.site,
        registros: item.registros,
        status: item.status,
        cultivares: item.cultivares,
        outroCultivo: item.outro_cultivo,
        metodo: item.metodo_cultivo,
        outroMetodo: item.outro_metodo,
        areaFisica: item.area_fisica_total,
        cultivoParalelo: item.outro_cultivo_paralelo,
        areaHidroponico: item.area_hidroponica,
        migracao: item.migrou_para_hidroponia,
        volume: item.vol_producao_total,
        profissao: item.profissao_anterior,
        localizacao: item.localizacao,
        movimentacao: item.movimentacao_financeira,
      };
      this.dialogPesquisa = true;
    },
    closePesquisa() {
      this.dialogPesquisa = false;
    },
  },
};
</script>
